#lightbox {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: none;
}

#lightbox.active {
  display: flex;
  justify-content: center;
  align-items: center;
  // width: 100%;

  &::before {
    font: normal 900 3em "Font Awesome 5 Free";
    content: "\f057";
    cursor: pointer;
    color: #fff;
    position: fixed;
    top: 5%;
    left: 85%;
  }
  img {
    width: 120%;
    height: auto;
    background: #fff;
    cursor: auto;
    
    @include tablet {
      width: 100%;

      @include notebook {
        width: 40%;
      }
    }
  }
}
